.greetingForm {
    margin-left: 70px;
    margin-top: 40px;
}

.inputAndButtonContainer {
    display: flex;
}

.error {
    margin-top: 7px;
    font-family: 'Montserrat';
    font-style: normal;
    font-weight: 400;
    font-size: 14px;
    line-height: 17px;
    color: #CC0000;
}

.input {
    width: 372px;
    height: 36px;
    border-radius: 5px;
    border: 1px solid #D1D1D1;

    font-family: 'Montserrat';
    font-style: normal;
    font-weight: 600;
    font-size: 16px;
    line-height: 20px;
    padding-left: 12px;

    color: #000000;
}

.input:focus {
    outline: none;
}

.errorInput {
    width: 372px;
    height: 36px;
    border-radius: 5px;
    border: 1px solid #CC0000;
}

.button {
    margin-left: 12px;
    width: 78px;
    height: 36px;
    border-radius: 3px;
    border: none;
    background: #0066CC;
    color: #FFFFFF;
    font-size: 14px;
    font-family: 'Montserrat', sans-serif;
    font-weight: 600;
    cursor: pointer;
}

.button:focus {
    border: 2px solid black;
}

.button:active {
    border: 2px solid black
}

.button:disabled {
    background: #0066CC;
    opacity: 0.5;
}

.text {
    margin-bottom: 9px;
    font-family: 'Montserrat', sans-serif;
    font-style: normal;
    font-weight: 400;
    font-size: 14px;
    line-height: 17px;
    color: #00000080;
}

.greeting {
    font-family: 'Montserrat';
    font-style: normal;
    font-weight: 600;
    font-size: 14px;
    line-height: 17px;

    color: #000000;
}
